import React, { Component } from 'react'
import IntroChapter from '../components/IntroChapter'
import bgImg from '../static/mcwinter.png'
import {connect} from 'react-redux'
import {getUserInfo} from '../reducers/user-info'
import {openLoginPage} from '../reducers/menus'

class IntroChapCr extends Component {
  render() {
    return (
      <div>
        <IntroChapter
          userInfo={this.props.getUserInfo}
          titleText="创意，创造不停"
          subTitleText="让孩子们感受到编程的乐趣"
          bgImg={bgImg}
          isShowButton={this.props.userInfo.isUserLogged}
          showLoginPage={this.props.openLoginPage}
        />
      </div>
    )
  }
}

const mapStateToProp = state => ({
  userInfo: getUserInfo(state)
})

const mapDispatchToProp = dispatch => ({
  openLoginPage: () => dispatch(openLoginPage())
})

export default connect(mapStateToProp,mapDispatchToProp)(IntroChapCr)