//用户信息

// action
const USER_LOGIN = 'user_login'
const USER_LOGOUT = 'user_logout'

// state key 
const USER_NAME = 'userName'
const USER_WORK_LIST = 'workList'
const USER_INTRO = 'userIntro'
const IS_USER_LOGGED = 'isUserLogged'

const initialState = {
  [IS_USER_LOGGED]: false,
  [USER_NAME]: '',
  [USER_INTRO]: '',
  [USER_WORK_LIST]: []
}

const reducer = (state=initialState, action) => {
  switch(action.type){
    case USER_LOGIN:
      return {
        ...state,
        [IS_USER_LOGGED]: true,
        [USER_NAME]: action.payload.userName,
        [USER_INTRO]: action.payload.introduce,
        [USER_WORK_LIST]: action.payload.worksList
      }
    case USER_LOGOUT:
      return {
        ...initialState
      }
    default:
      return state
  }
}

const loadUserInfo = info => ({
  type: USER_LOGIN,
  payload: info
})

const logout = () => ({
  type: USER_LOGOUT
})

const getUserInfo = state => state.userInfo
const isUserLogged = state => state.userInfo[IS_USER_LOGGED]

export {
  reducer as default,
  loadUserInfo,
  logout,
  getUserInfo,
  isUserLogged
}
