import React from 'react'
import {withStyles} from '@material-ui/core/styles'
import {ExpansionPanel, ExpansionPanelSummary,Typography, ExpansionPanelDetails} from '@material-ui/core'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'
import qrcode from '../../static/qrcode.png'

const ConnectMe = ({classes}) => (
  <div className={classes.root}>
    <ExpansionPanel className={classes.expandpanel}>
        <ExpansionPanelSummary expandIcon={<ExpandMoreIcon styles={{color: 'white'}}/>}>
          <Typography className={classes.heading}>制作者:王庆云&nbsp;&nbsp;&nbsp;&nbsp;联系方式：</Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <div>
            <Typography>
              手机号码:1850683035 &nbsp;&nbsp;|&nbsp;&nbsp; 邮箱:qingyun@outlook.com
            </Typography>
            <img src={qrcode} alt="qrcode" className={classes.qrcode}/>
          </div>
        </ExpansionPanelDetails>
      </ExpansionPanel>
  </div>
)

const styles = theme => ({
  root: {
    position: 'relative',
    margin: 'auto',
    width: 500,
    marginTop: theme.spacing.unit * 8
  },
  qrcode: {
    width: '100%'
  },
  heading: {
    fontWeight: 'bold'
  }
})

export default withStyles(styles)(ConnectMe)