import React from 'react'
import {withStyles} from '@material-ui/core/styles'
import {Button, IconButton, Avatar} from '@material-ui/core'
import LeftIcon from '@material-ui/icons/KeyboardArrowLeft'
import RightIcon from '@material-ui/icons/KeyboardArrowRight'
import classnames from 'classnames'

const Banner = ({
  classes, 
  imgSet, 
  openLoginPage,
  userInfo
}) => {
  return (
    <div className={classes.root}>
      <ul className={classes.sliderContainer}>
        <div className={classes.slider} id='slider'>
          {
            imgSet.map((item, index) => 
              <li className={classes.sliderItem} style={{marginLeft: 950 * index}} key={index}>
                <img src={item} 
                  alt="space" 
                  className={classes.bannerImg}
                />
              </li>)
          } 
        </div>
        <IconButton 
          className={classnames(classes.sliderBtn,classes.btnLeft)}
          onClick={() => handleSilder('left')}
        ><LeftIcon /></IconButton>
        <IconButton 
          className={classnames(classes.sliderBtn,classes.btnRight)}
          onClick={() => handleSilder('right')}
        ><RightIcon /></IconButton>
      </ul>
      <div className={classes.bannerRight}>
        <div className={classes.joinus}>
          <Avatar
            className={classes.avatar}
            src={userInfo.avatar ? userInfo.avatar : "http://localhost:8800/avatar/avatar.png"}
          />
          <p>{userInfo.userName}</p>
          <p>{userInfo.isUserLogged 
            ? userInfo.introduce ?  userInfo.introduce : '目前还不能说什么'
            : "欢迎来到这里，请登陆"}</p>
          {
            userInfo.isUserLogged ? '' : <Button variant="outlined" onClick={openLoginPage}>登陆/注册</Button>
          }
        </div>
        <a href="http://localhost:8800/requestfile">
        <Button
          size="large"
          variant="contained"
          className={classes.createBtn}
          color="primary"
        >开始创作</Button>
        </a>
      </div>
    </div>
  )
}

const handleSilder = (direction) => {
  const slider = document.getElementById('slider')
  console.log(slider.style.left)
  let position = slider.style.left
  if (direction === 'right') {
    slider.style.left = position + 950
  } else if (direction === 'left') {
    slider.style.left = position + 950
  }
  console.log(slider.style.left)
}

const styles = theme => ({
  root: {
    display: 'flex',
    height: 350
  },
  sliderContainer: {
    position: 'relative',
    overflow: 'hidden',
    padding: 0,
    margin: 0,
    width: 950,
    height: 350,
  },
  sliderItem: {
    position: 'absolute',
    listStyle: 'none',
    boxSizing: 'border-box',
    width: 950,
    '& img': {
      maxWidth: '100%'
    }
  },
  sliderBtn: {
    position: 'absolute',
    top: '50%',
    backgroundColor: 'rgba(255,255,255,.3)'
  },
  btnLeft: {
    left: 10
  },
  btnRight: {
    right: 10
  },
  slider: {
    // transform: 'translate(50px)'
  },
  bannerRight: {
    boxSizing: 'border-box',
    paddingLeft: theme.spacing.unit,
    paddingRight: theme.spacing.unit,
    textAlign: 'center',
    width: 250,
    height: '100%'
  },
  joinus: {
    backgroundColor: 'white',
    height: 280,
    border: '1px solid #eee',
    borderRadius: 5,
    fontSize: 15,
    '& p': {
      // marginTop: 100
    }
  },
  createBtn: {
    marginTop: theme.spacing.unit * 2,
    width: '95%',
    color: theme.palette.common.white
  },
  avatar: {
    position: 'relative',
    marginLeft: 'auto',
    marginRight: 'auto',
    marginTop: theme.spacing.unit * 6,
    width: 100,
    height: 100,
  }
})

export default withStyles(styles)(Banner)
