import React from 'react';
import { TextField, Button, IconButton, Card, CardHeader, CardContent, CardActions, Typography } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import CloseIcon from '@material-ui/icons/Close';

const LoginRegisterPage = ({
  classes,
  handleRegisterNameChange,
  handleRegisterPwdChange,
  registerName,
  registerPwd,
  handleConfrimChange,
  pwdConfirm,

  handleLoginNameChange,
  handleLoginPwdChange,
  loginName,
  loginPwd,

  isShowLogin,
  showLogin,
  showRegister,

  loginCommit,
  registerCommit,
  handlePageClose,


  nameHelpText,
  getTestResult,

  isShowRegisterSuccess,
  handleGoLogin
  
}) => {
  //登陆页
  const LoginPage = (
    <CardContent key="login">
      <CardActions className={classes.login_actions}>
        <Typography variant="caption">
          还未加入我们?
          <Button color="primary" onClick={showRegister}>
            注册
          </Button>
        </Typography>
      </CardActions>
      <TextField
        label="用户名"
        variant="outlined"
        helperText={
          getTestResult('loginName') ? "请输入用户名用户名" : "格式不正确"
        }
        error={!getTestResult('loginName')}
        className={classes.textField}
        onChange={handleLoginNameChange}
        value={loginName}
      />
      <TextField
        label="密码"
        variant="outlined"
        type="password"
        helperText={
          getTestResult('loginPwd') ? "请输入密码" : "密码格式不正确"
        }
        className={classes.textField}
        onChange={handleLoginPwdChange}
        value={loginPwd}
        error={!getTestResult('loginPwd')}
      />
      <div>
        <Button 
          variant="contained" 
          color="primary" 
          className={classes.btns}
          onClick={loginCommit}
        >登陆</Button>
        <Button 
          variant="contained" 
          color="primary" 
          className={classes.btns}
          onClick={handlePageClose}
        >取消</Button>
      </div>
    </CardContent>
  )
  //注册页
  const RegisterPage = (
    <CardContent key="register">
      <CardActions className={classes.login_actions}>
        <Typography variant="caption">
          已经有账号?
          <Button color="primary" onClick={showLogin}>
            登陆
          </Button>
        </Typography>
      </CardActions>
      <TextField
        label="用户名"
        variant="outlined"
        helperText={nameHelpText}
        className={classes.textField}
        onChange={handleRegisterNameChange}
        value={registerName}
        error={!getTestResult('registerName')}
      />
      <TextField
        label="密码"
        variant="outlined"
        type='password'
        helperText="8~20位字母数字组合"
        className={classes.textField}
        onChange={handleRegisterPwdChange}
        value={registerPwd}
        error={!getTestResult('registerPwd')}
      />
      <TextField
        label="确认密码"
        variant="outlined"
        type='password'
        helperText={
          getTestResult('confirmPwd') ? "确认密码" : "密码不一致"
        }
        className={classes.textField}
        value={pwdConfirm}
        error={!getTestResult('confirmPwd')}
        onChange={handleConfrimChange}
      />
      <div>
        <Button 
          variant="contained" 
          color="primary" 
          className={classes.btns}
          onClick={registerCommit}
        >提交</Button>
        <Button 
          variant="contained" 
          color="primary" 
          className={classes.btns}
          onClick={handlePageClose}
        >取消</Button>
      </div>
    </CardContent>
  )

  //注册成功页
  const registerSuccess = (
    <CardContent>
      <h2>注册成功</h2>
      <Button 
          variant="contained" 
          color="primary" 
          className={classes.btns}
          onClick={handleGoLogin}
        >请登陆</Button>
    </CardContent>
  )

  return (
    <div className={classes.loginOverLay}>
      <Card className={classes.loginCard}>
        <CardHeader
          title={isShowLogin ? '登陆' : '注册'}
          action={ 
            <IconButton>
              <CloseIcon onClick={handlePageClose}/>
            </IconButton>
          }
        />
        {
          isShowRegisterSuccess 
          ? registerSuccess 
          : (isShowLogin ? LoginPage : RegisterPage)
        }
      </Card>
    </div>
  )
}
    
const styles = theme => ({
  loginOverLay: {
    position: 'fixed',
    left: 0,
    top: 0,
    width: '100vw',
    height: '100%',
    zIndex: 1000,
  },
  loginCard: {
    position: 'fixed',
    width: 500,
    marginTop: theme.spacing.unit * 16,
    left: '50%',
    marginLeft: -250,
    paddingLeft: 30,
    paddingRight: 30,
    textAlign: 'center'
  },
  login_actions: {
    justifyContent: 'space-between'
  },
  textField: {
    width: '100%',
    marginTop: 15,
    marginBottom: 15
  },
  btns: {
    color: theme.palette.common.white,
    marginLeft: 5,
    marginRight: 5,
  }
})

export default withStyles(styles)(LoginRegisterPage);
