import React, { Component } from 'react'
import Header from '../components/Header'
import {connect} from 'react-redux'
import {getUserInfo, logout} from '../reducers/user-info'
import {openLoginPage} from '../reducers/menus'

class HeaderContainer extends Component {

  handleLogout = () => {
    let formData = new FormData()
      formData.append('userName', 'null')
      formData.append('password', 'null')

    fetch('http://localhost:8800/users/login', {
      method: 'POST',
      credentials: 'include',
      body: formData
    })
    .then(res => res.json())
    .then(data => {
      //todo
    })
    .catch(err => {
      console.log(err)
    })
    this.props.logout()
  }

  render() {
    return (
      <div>
        <Header 
          userInfo={this.props.userInfo}
          openLoginPage={this.props.openLoginPage}
          logout={this.handleLogout}
        />
      </div>
    )
  }
}

const mapStateToProps = state => ({
  userInfo: getUserInfo(state)
})

const mapDispatchToProps = dispatch => ({
  openLoginPage: () => dispatch(openLoginPage()),
  logout: () => dispatch(logout())
})

export default connect(mapStateToProps,mapDispatchToProps)(HeaderContainer)