import React,{Component} from 'react'
import CourseCard from '../components/CourseCard'
import Dialog from '../components/Dialogs'
import {connect} from 'react-redux'
import {
  isDialogOpen,
  openDialog,
  closeDialog 
} from '../reducers/menus'

class CourseCardContainer extends Component {

  state={
    courseInfo: []
  }

  componentDidMount() {
    this.getCourserInfo()
  }

  getCourserInfo = () => {
    fetch('http://localhost:8800/mock/course.json',{
      method: 'GET'
    })
    .then(res => res.json())
    .then(result => {
      if (result.statu === 'ok') {
        this.setState({
          courseInfo: result.data
        })
      }
    })
    .catch(err => {
      console.log(err)
    })
  }

  render() {
    return (
      <div>
        <h3>精品课堂</h3>
        <div>
          {
            this.state.courseInfo.map(item => <CourseCard openDialog={this.props.openDialog} courseInfo={item} key={item.id}/>)
          }
        </div>
        <Dialog 
          isDialogOpen={this.props.isDialogOpen}
          openDialog={this.props.openDialog}
          closeDialog={this.props.closeDialog}
        />
      </div>
    )
  }
}

const mapStateToProps = state => ({
  isDialogOpen: isDialogOpen(state)
})

const mapDispatchToProps = dispatch => ({
  openDialog: () => dispatch(openDialog()),
  closeDialog: () => dispatch(closeDialog())
})

export default connect(mapStateToProps,mapDispatchToProps)(CourseCardContainer)