import React from 'react'
import {withStyles} from '@material-ui/core/styles'
import {Button} from '@material-ui/core'

const IntroChapter = ({
  classes,
  userInfo,
  titleText,
  subTitleText,
  bgImg,
  isShowButton,
  showLoginPage
}) => {
  return (
    <div className={classes.row}>
      <div className={classes.rowLeft}>
        <h1>{titleText}</h1>
        <p>{subTitleText}</p>
        <Button
          variant="contained"
          size="large"
          color="secondary"
          onClick={showLoginPage}
          className={
            isShowButton
            ? classes.hide
            : classes.joinusBtn
          }
        >加入我们</Button>
      </div>
      <div className={classes.mcWinter}>
        <img src={bgImg} alt="mc" />
      </div>
    </div>
  )
} 

const styles = theme => ({
  row: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    width: '100%',
    position: 'relative',
    marginLeft: 'auto',
    marginRight: 'auto',
    paddingTop: theme.spacing.unit * 16,
    [theme.breakpoints.down('md')]: {
      flexDirection: 'column',
    }
  },
  rowLeft: {
    position: 'relative',
    width: 300,
    textAlign: 'center',
    color: theme.palette.common.white
  },
  mcWinter: {
    position: 'relative',
    flexShrink: 0,
    width: 900,
    marginLeft: theme.spacing.unit * 8,
    [theme.breakpoints.down('xs')]: {
      width: 400,
      marginTop: 90
    },
    '& img': {
      maxWidth: '100%'
    }
  },
  joinusBtn: {
    color: theme.palette.common.white
  },
  hide: {
    opacity: 0
  }
})

export default withStyles(styles)(IntroChapter)

