import React, { Component } from 'react'
import LoginPage from '../components/Login'
import {connect} from 'react-redux'
import {loadUserInfo} from '../reducers/user-info'
import {closeLoginPage, isLoginPageOpen} from '../reducers/menus'
import {Dialog} from '@material-ui/core'
import {
  registerInputName,
  registerInputPwd,
  getRegisterName,
  getRegisterPwd,
  confirmPwdInput,
  confirmPwd,

  loginInputName,
  loginInputPwd,
  getLoginName,
  getLoginPwd,

  isShowLogin,
  showLogin,
  showRegister,

  clearAll,
  getNameHelpText,
  setNameHelpText,
  
  setTestResult,
  getTestResult,

  isShowRegisterSuccess,
  showRegisterSuccess
} from '../reducers/account-status'

class LoginContainer extends Component {

  //用户名密码正则验证
  nameTest = (name) => {
    const nameReg = /^[a-zA-Z]{1}([a-zA-Z0-9]|[._]){4,12}$/
    return name ? nameReg.test(name) : true
  }

  pwdTest = (pwd) => {
    const pwdReg = /^[a-zA-Z0-9]{8,20}$/
    return pwdReg.test(pwd)
  }

  //注册操作
  registerCommit = () => {
    if ( this.props.registerName &&
         this.props.registerPwd &&
         this.props.getTestResult('registerName') &&
         this.props.getTestResult('confirmPwd') &&
         this.props.getTestResult('registerPwd')){
           
      let formData = new FormData()
      formData.append('registerName', this.props.registerName)
      formData.append('registerPwd', this.props.registerPwd)

      fetch('http://localhost:8800/users/register', {
        method: 'POST',
        credentials: 'include',
        body: formData
      })
      .then(res => res.json())
      .then(data => {
        switch(data.status) {
          case 0:
            this.props.showRegisterSuccess(true)
            break
          case 2:
            this.props.setNameHelpText('用户名已存在')
            this.props.setTestResult('registerName', false)
            break
          default:
            this.props.setNameHelpText('未知错误，请重试')
            this.props.setTestResult('registerName', false)
        }
      })
      .catch(err => {
        console.log(err)
      })
    }
  }

  //登陆操作
  login = () => {

    let formData = new FormData()
      formData.append('userName', this.props.loginName)
      formData.append('password', this.props.loginPwd)

    fetch('http://localhost:8800/users/login', {
      method: 'POST',
      credentials: 'include',
      body: formData
    })
    .then(res => res.json())
    .then(data => {
      if (data.status === 2) {
        // 把用户信息载入到store中
        this.props.loadUserInfo(data.userInfo)
        // 清除信息
        this.props.closeLoginPage()
        this.props.clearAll()
      }
    })
    .catch(err => {
      console.log(err)
    })
  }

  //如果有cookie，自动登陆
  componentDidMount() {
    this.login()
  }

  //防止两个action执行顺序调换
  handleRegisterNameChange = (e) => {
    this.props.registerInputName(e)
    setTimeout(() => {
      this.props.setTestResult('registerName', this.nameTest(this.props.registerName))
    })
  }
  handleRegisterPwdChange = e => {
    this.props.registerInputPwd(e)
    setTimeout(() => {
      this.props.setTestResult('registerPwd', this.pwdTest(this.props.registerPwd))
    }, 300)
  }
  handleConfrimChange = e => {
    this.props.confirmPwdInput(e)
    setTimeout(() => {
      this.props.setTestResult('confirmPwd', this.props.pwdConfirm === this.props.registerPwd)
    }, 300)
  }
  handleLoginNameChange = e => {
    this.props.loginInputName(e)
    setTimeout(() => {
      this.props.setTestResult('loginName', this.nameTest(this.props.loginName))
    }, 300)
  }
  handleLoginPwdChange = e => {
    this.props.loginInputPwd(e)
    setTimeout(() => {
      this.props.setTestResult('loginPwd', this.pwdTest(this.props.loginPwd))
    }, 3)
  }
  handlePageClose = () => {
    this.props.closeLoginPage()
    this.props.clearAll()
  }
  handleGoLogin = () => {
    this.props.clearAll()
    this.props.showRegisterSuccess(false)
  }

  render() {
    return (
      // <div style={this.props.isLoginPageOpen 
      //   ? {display: 'flex'}
      //   : {display: 'none'}}
      // >
      <Dialog open={this.props.isLoginPageOpen}>
         <LoginPage      
              handleRegisterNameChange={this.handleRegisterNameChange}
              handleRegisterPwdChange={this.handleRegisterPwdChange}
              registerName={this.props.registerName}
              registerPwd={this.props.registerPwd}
              pwdConfirm={this.props.pwdConfirm}
              handleConfrimChange={this.handleConfrimChange}

              isShowLogin={this.props.isShowLogin}
              showLogin={this.props.showLogin}
              showRegister={this.props.showRegister}

              handleLoginNameChange={this.handleLoginNameChange}
              handleLoginPwdChange={this.handleLoginPwdChange}
              loginName={this.props.loginName}
              loginPwd={this.props.loginPwd}

              registerCommit={this.registerCommit}
              loginCommit={this.login}
              handlePageClose={this.handlePageClose}
              clearAll={this.props.clearAll}

              nameHelpText={this.props.getNameHelpText}
              getTestResult={this.props.getTestResult}
              setTestResult={this.props.setTestResult}

              isShowRegisterSuccess={this.props.isShowRegisterSuccess}
              handleGoLogin={this.handleGoLogin}
            />
      </Dialog>
    )
  }
}

const mapStateToprops = state => ({
  registerName: getRegisterName(state),
  registerPwd: getRegisterPwd(state),
  pwdConfirm: confirmPwd(state),
  isShowLogin: isShowLogin(state),
  loginName: getLoginName(state),
  loginPwd: getLoginPwd(state),
  isLoginPageOpen: isLoginPageOpen(state),
  getNameHelpText: getNameHelpText(state),
  getTestResult: type => getTestResult(state,type),
  isShowRegisterSuccess: isShowRegisterSuccess(state)
})

const mapDispatchToProps = dispatch => ({
  registerInputName: e => dispatch(registerInputName(e.target.value)),
  registerInputPwd: e => dispatch(registerInputPwd(e.target.value)),
  confirmPwdInput: e => dispatch(confirmPwdInput(e.target.value)),
  showLogin: () => dispatch(showLogin()),
  showRegister: () => dispatch(showRegister()),
  loginInputName: e => dispatch(loginInputName(e.target.value)),
  loginInputPwd: e => dispatch(loginInputPwd(e.target.value)),
  clearAll: () => dispatch(clearAll()),
  loadUserInfo: info => dispatch(loadUserInfo(info)),
  closeLoginPage: () => dispatch(closeLoginPage()),
  setNameHelpText: text => dispatch(setNameHelpText(text)),
  setTestResult: (type, bool) => dispatch(setTestResult(type, bool)),
  showRegisterSuccess: bool => dispatch(showRegisterSuccess(bool))
})

export default connect(mapStateToprops,mapDispatchToProps)(LoginContainer)
