import React from 'react'
import {withStyles} from '@material-ui/core/styles'
import {Grid} from '@material-ui/core'
import WorksCard from '../WorksCard'

const Works = ({classes, worksData, title, size}) => {
  return (
    <Grid container justify='center' spacing={8} className={classes.gridContainer}>
      <Grid item xs={12}>
        <h3>{title}</h3>
      </Grid>
      {
        worksData.map(data => (
          <Grid 
            item xs={12} sm={6} md={4} lg={3} 
            key={data.id} 
            className={classes.gridItem}
          >
            <a href={data.urlLink}> 
              <WorksCard 
                cover={data.cover}
                title={data.title}
                author={data.author}
                likes={data.likes}
                views={data.views}
                avatar={data.avatar}
                size={size}
              />
            </a>
          </Grid>
        ))
      }
    </Grid>
  )
}

const styles = theme => ({
  gridContainer: {
    width: 1200,
    marginLeft: 'auto',
    marginRight: 'auto',
    [theme.breakpoints.down('md')]: {
      width: 950
    },
    [theme.breakpoints.down('sm')]: {
      width: 600
    },
    [theme.breakpoints.down('xs')]: {
      width: 300
    },
  },
  gridItem: {
    display: 'flex',
    justifyContent: 'center'
  }
})

export default withStyles(styles)(Works)