import React from 'react';
import {withStyles} from '@material-ui/core/styles';

const Footer = props => {
  const {classes} = props
  return (
      <div className={ classes.footer }>
          <div>
            <p>@2019 QY网络（杭州） 有限公司版权所有</p>
            <p>浙ICP备 18506830235号</p>
          </div>
          <div>
            <div>
              <span className={classes.footerLink}>帮助中心&nbsp;|&nbsp;</span>
              <span className={classes.footerLink}>加入我们&nbsp;|&nbsp;</span>
              <span className={classes.footerLink}>使用条例&nbsp;|&nbsp;</span>
              <span className={classes.footerLink}>隐私政策&nbsp;|&nbsp;</span>
              <span className={classes.footerLink}>中国</span>
            </div >
            <p>浙公网安备 qingyun@outlook.com</p>
          </div>
      </div>
    )
}
  
const styles = theme => ({
  footer: {
    position: 'relative',
    width: '100%',
    paddingLeft: theme.spacing.unit * 6,
    paddingRight: theme.spacing.unit * 6,
    marginTop: theme.spacing.unit * 16,
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    backgroundColor: theme.palette.common.white,
    [theme.breakpoints.down('md')]: {
      display: 'block',
      paddingTop:theme.spacing.unit * 2,
      paddingBottom:theme.spacing.unit * 2
    }
  },
  
  footerLink: {
    color: '#888',
  }
})

export default withStyles(styles)(Footer);