import Works from '../components/Works'
import React, { Component } from 'react'

const styles = {
  root: {
    backgroundColor: 'rgb(245, 245, 245)',
    paddingBottom: 80
  }
}

export default class WorksContainer extends Component {
  
  state = {
    hotWorks: [],
    selectedWorks: []
  }

  componentDidMount() {
    fetch('http://localhost:8800/mock/works.json',{
      method: 'GET'
    })
    .then(res => res.json())
    .then(result => {
      if (result.statu === 'ok') {
        
        this.setState({
          hotWorks: result.data.hot,
          selectedWorks: result.data.selected
        })
      }
    })
    .catch(err => {
      console.log(err)
    })
  }
 
  render() {
    return (
      <div 
        style={styles.root}
      >
        <Works 
          worksData={this.state.hotWorks}
          title={'精选作品'}
        />
        <Works 
          worksData={this.state.selectedWorks}
          title={'热门作品'}
        />
      </div>
    )
  }
}
