// 所有菜单的数据

// acton
const OPEN_MENU = 'OPEN_MENU'
const CLOSE_MENU = 'CLOSE_MENU'

// state keys
const LOGIN_PAGE = 'loginPage'
const DIALOGS = 'Dialogs'

const initialState = {
  [LOGIN_PAGE]: false,
}

const reducers = (state=initialState, action) => {
  switch (action.type) {
    case OPEN_MENU:
      return {
        ...state,
        [action.menu]: true
      }
    case CLOSE_MENU:
      return {
        ...state,
        [action.menu]: false
      }
    default:
      return state
  }
}

const openMenu = menu => ({
  type: OPEN_MENU,
  menu
})


const closeMenu = menu => ({
  type: CLOSE_MENU,
  menu
})

// action creators
const openLoginPage = () => openMenu(LOGIN_PAGE)
const closeLoginPage = () => closeMenu(LOGIN_PAGE)
const openDialog = () => openMenu(DIALOGS)
const closeDialog = () => closeMenu(DIALOGS)

// get state
const isLoginPageOpen = state => state.homeUi.menus[LOGIN_PAGE]
const isDialogOpen = state => state.homeUi.menus[DIALOGS]

export {
  reducers as default,
  openLoginPage,
  closeLoginPage,
  isLoginPageOpen,
  isDialogOpen,
  openDialog,
  closeDialog
}