import React, { Component } from 'react'
import Banner from '../components/Banner'
import space from '../static/space.png'
import foot from '../static/foot.png'
import {openLoginPage} from '../reducers/menus'
import {getUserInfo} from '../reducers/user-info'
import {connect} from 'react-redux'

class BannerContainer extends Component {
  render() {
    const imgSet = [space, foot]
    return (
      <div>
        <Banner
          imgSet={imgSet}
          openLoginPage={this.props.openLoginPage}
          userInfo={this.props.userInfo}
        />
      </div>
    )
  }
}

const mapStateToProps = state => ({
  userInfo: getUserInfo(state)
})

const mapDispatchToProps = dispatch =>({
  openLoginPage: () => dispatch(openLoginPage())
})

export default connect(mapStateToProps,mapDispatchToProps)(BannerContainer)
