import React,{Component} from 'react'
import ProfileBody from '../components/ProfileBody'
import {getUserInfo} from '../reducers/user-info'
import {connect} from 'react-redux'

class ProfileBodyContainer extends Component {
  render() {
    return (
      <div>
        <ProfileBody 
          worksData={this.props.userInfo}
        />
      </div>
    )
  }
}

const mapStateToProps = state => ({
  userInfo: getUserInfo(state)
})

export default connect(mapStateToProps)(ProfileBodyContainer)
