import React from 'react'
import ketang from '../../static/ketang.gif'
import {withStyles} from '@material-ui/core/styles'

const CourseTitle = ({classes}) => (
  <div className={classes.root}>
    <img src={ketang} alt="ketang" className={classes.background}/>
  </div>
)

const styles = theme =>({
  root: {
    position: 'relative',
    marginTop: theme.spacing.unit * 8,
    width: '100%',
    height: 300
  },
  background: {
    width: 1200
  }
})

export default withStyles(styles)(CourseTitle)

