import React from 'react'
import {withStyles} from '@material-ui/core/styles'
import {Card, Button, Tooltip} from '@material-ui/core'
import AssignmentInd from '@material-ui/icons/AssignmentInd'

const ProfileInfo = ({classes, userName, userIntro}) => (
  <div className={classes.root}>
     <Card>
        <div className={classes.cardHeader}>
          <span>基本信息</span>
          <Tooltip title="开发中..." placement="left">
            <Button variant="outlined" size="small" className={classes.editBtn }>
              编辑个人资料
            </Button>
          </Tooltip>
        </div>
        <div className={classes.cardContent}>
            <AssignmentInd className={classes.avatar} />
            <div>
              <p className={classes.userName}>{userName ? userName : '未登录'}</p>
              <p className={classes.userIntro}>{userIntro ? userIntro : '这家伙有点懒，什么都没说'}</p>
            </div>
        </div>
      </Card>
  </div>
)

const styles = theme => ({
  root: {
    width: '100%'
  },
  cardHeader: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    height: theme.spacing.unit * 4,
    padding: theme.spacing.unit,
    fontSize: '.8rem',
    backgroundColor: theme.palette.primary.dark
  },
  cardContent: {
    display: 'flex',
    minHeight: 100,
    padding: theme.spacing.unit
  },
  avatar: {
    width: 100,
    height: 100,
    marginRight: theme.spacing.unit
  },
  userName: {
    fontSize: 15,
    fontWeight: 'bold',
    color: '#666'
  },
  userIntro: {
    fontSize: 10,
    color: 'grey'
  },
  editBtn: {
    height: '20px',
    fontSize: '5px',
    lineHeight: '20px',
    padding: 0
  }
})

export default withStyles(styles)(ProfileInfo)
