import React from 'react'
import {withStyles} from '@material-ui/core/styles'
import {Typography, Grid} from '@material-ui/core'
import iconSet from '../../static'

const IntroSection = props => {
  const {classes} = props
  return (
    <div className={classes.root}>
      <Typography variant='h5'>
        图形化的编程界面
      </Typography>
      <Typography variant='subtitle1'>
        感受编程的乐趣，迎接未来的挑战
      </Typography>
      <Grid container spacing={8} className={classes.iconContainer}>
        {
          iconSet.map(item => (
            <Grid item xs={6} md={2} sm={4} key={item.id} className={classes.iconItem}>
              <p>{item.text}</p>
              <img src={item.icon} alt="icon1"/>
            </Grid>
          ))
        }
      </Grid>
    </div>
  )
}

const styles = theme => ({
  root: {
    position: 'relative',
    textAlign: 'center',
    marginTop: theme.spacing.unit * 6,
    marginBottom: theme.spacing.unit * 6,
  },
  iconContainer: {
    width: '60%',
    marginLeft: 'auto',
    marginRight: 'auto',
    marginTop: theme.spacing.unit * 4
  },
  iconItem: {
    '& img': {
      maxWidth: 50
    }
  }
})

export default withStyles(styles)(IntroSection)