// action type
const SHOW_LOGIN = 'switchlogreg'
const SHOW_REGISTER = 'showRegister'
const INPUT = 'input'
const LOG_IN = 'login'
const LOG_OUT = 'logout'
const CLEAR_ALL = 'clearAll'
const REGISTER_NAME_TEST = 'nameRegularTest'
const REGISTER_PWD_TEST = 'passwordRegTest'
const CONFIRM_PWD_TEST = 'confirmPwdTest'
const LOGIN_NAME_TEST = 'loginNameTest'
const LOGIN_PWD_TEST = 'loginPwdTest'

// state key
const IS_SHOW_LOGIN = 'isShowLogin'
const REG_NAME = 'reg_Name'
const REG_PWD = "reg_pwd"
const PWD_CONFIRM = 'passwordconfirm'
const LOGIN_NAME = 'loginName'
const LOGIN_PWD = 'loginPwd'
const CAN_COMMIT = 'canCommit'
const NAME_HELP_TEXT = 'nameHelpText'
const ISSHOW_REGISTER_SUCCESS = 'isshowregishtersuccess'

const initialState = {
  [IS_SHOW_LOGIN]: true,
  [REG_NAME]: '',
  [REG_PWD]: '',
  [PWD_CONFIRM]: '',
  [LOGIN_NAME]: '',
  [LOGIN_PWD]: '',
  [CAN_COMMIT]: false,
  [NAME_HELP_TEXT]: '用户名为5~12位数字字母的组合',
  [REGISTER_NAME_TEST]: true,
  [REGISTER_PWD_TEST]: true,
  [CONFIRM_PWD_TEST]: true,
  [LOGIN_NAME_TEST]: true,
  [LOGIN_PWD_TEST]: true,
  [ISSHOW_REGISTER_SUCCESS]: false
}

const reducer = (state=initialState, action) => {
  switch(action.type) {
    case SHOW_LOGIN:
      return {
        ...state,
        [IS_SHOW_LOGIN]: true
      }
    case SHOW_REGISTER:
      return {
        ...state,
        [IS_SHOW_LOGIN]: false
      }
    case INPUT:
      return {
        ...state,
        [action.payload.seletor]: action.payload.value
      }
    case CLEAR_ALL:
      return {
        ...initialState
      }
    default:
      return state
  }
}
// action creator
const showLogin = () => ({
  type: SHOW_LOGIN
})

const showRegister = () => ({
  type: SHOW_REGISTER
})

const input = (seletor, value) => ({
  type: INPUT,
  payload: {
    seletor,
    value
  }
})

const logIn = () => ({
  type: LOG_IN
})
   
const logOut = () => ({
    type: LOG_OUT
})

// actions creator
const registerInputName = name => input(REG_NAME, name)
const registerInputPwd = pwd => input(REG_PWD, pwd)
const confirmPwdInput = pwd => input(PWD_CONFIRM, pwd)
const loginInputName = name => input(LOGIN_NAME, name)
const loginInputPwd = pwd => input(LOGIN_PWD, pwd)
const setNameHelpText = text => input(NAME_HELP_TEXT, text)
const clearAll = () => ({ type: CLEAR_ALL })
const showRegisterSuccess = bool => input(ISSHOW_REGISTER_SUCCESS, bool)
const setTestResult = (seletor, bool) => {
  switch(seletor) {
    case 'registerName':
      return input(REGISTER_NAME_TEST ,bool)
    case 'registerPwd':
      return input(REGISTER_PWD_TEST ,bool)
    case 'confirmPwd':
      return input(CONFIRM_PWD_TEST ,bool)
    case 'loginName':
      return input(LOGIN_NAME_TEST ,bool)
    case 'loginPwd':
      return input(LOGIN_PWD_TEST ,bool)
    default:
      return
  }
}

// get state
const isShowLogin = state => state.accountStatus[IS_SHOW_LOGIN]
const getRegisterName = state => state.accountStatus[REG_NAME]
const getRegisterPwd = state => state.accountStatus[REG_PWD]
const confirmPwd = state => state.accountStatus[PWD_CONFIRM]
const getLoginName = state => state.accountStatus[LOGIN_NAME]
const getLoginPwd = state => state.accountStatus[LOGIN_PWD]
const getNameHelpText = state => state.accountStatus[NAME_HELP_TEXT]
const isShowRegisterSuccess = state => state.accountStatus[ISSHOW_REGISTER_SUCCESS]
const getTestResult = (state,seletor) => {
  switch(seletor) {
    case 'registerName':
      return state.accountStatus[REGISTER_NAME_TEST]
    case 'registerPwd':
     return state.accountStatus[REGISTER_PWD_TEST]
    case 'confirmPwd':
      return state.accountStatus[CONFIRM_PWD_TEST]
    case 'loginName':
      return state.accountStatus[LOGIN_NAME_TEST]
    case 'loginPwd':
      return state.accountStatus[LOGIN_PWD_TEST]
    default:
      break
  }
}

export {
  reducer as default,
  logIn,
  logOut,

  showLogin,
  showRegister,
  isShowLogin,

  registerInputName,
  registerInputPwd,
  getRegisterName,
  getRegisterPwd,
  confirmPwdInput,
  confirmPwd,

  loginInputName,
  loginInputPwd,
  getLoginName,
  getLoginPwd,

  clearAll,
  getNameHelpText,
  setNameHelpText,

  setTestResult,
  getTestResult,

  isShowRegisterSuccess,
  showRegisterSuccess
}