import React from 'react'
import {withStyles} from '@material-ui/core/styles'
import classnames from 'classnames'
import {Card, CardMedia, Divider, Avatar} from '@material-ui/core'
import VisibilityIcon from '@material-ui/icons/Visibility'
import ThumbupIcon from '@material-ui/icons/ThumbUp'

const WorksCard = ({
  classes, 
  cover, 
  title,
  likes,
  views,
  avatar,
  author,
  size}) => {

  return (
    <Card className={
      size === 'small'
      ? classnames(classes.card,classes.cardSmall)
      : classnames(classes.card,classes.cardBig)
    }>
      <CardMedia
        image={cover}
        className={classes.cardMedia}
      />
      <div className={classes.cardContent}>
        <p className={classes.subText}>
          {title}
          <span className={classes.views}>
            <VisibilityIcon className={classes.icons}/>
            {views ? views : 0}
          </span>
          <span className={classes.views}>
            <ThumbupIcon className={classes.icons}/>
            {likes ? likes : 0}
          </span>
        </p>
        <Divider />
        <div className={classes.cardBottom}>
          <Avatar src={avatar} className={classes.avatar} />
          <span className={classes.subText}>
            {author}
          </span>
        </div>
      </div>    
    </Card>
  )
}


const styles = theme => ({
  card: {
    position: 'relative',
    marginRight: 0,
    marginTop: theme.spacing.unit * 2,
    cursor: 'pointer'
  },
  cardSmall: {
    width: 200
  },
  cardBig: {
    width: 250
  },
  cardMedia: {
    height: 0,
    paddingBottom: '80%'
  },
  cardContent: {
    paddingLeft: theme.spacing.unit,
    paddingRight: theme.spacing.unit
  },
  subText: {
    fontSize: '.7rem',
    color: '#666',
    marginTop: theme.spacing.unit * 2,
    marginBottom: theme.spacing.unit * 2
  },
  views: {
    dispaly: 'inline-block',
    float: 'right',
    marginLeft: theme.spacing.unit * 2
  },
  icons: {
    fontSize: 15,
    color: theme.palette.background.dark,
    marginRight: 1
  },
  cardBottom: {
    display: 'flex',
    alignItems: 'center',
    marginTop: theme.spacing.unit
  },
  avatar: {
    width: 30,
    height: 30,
    marginRight: theme.spacing.unit
  }
})

export default withStyles(styles)(WorksCard)