import React from 'react'
import {withStyles} from '@material-ui/core/styles'
import {
  Button,
  IconButton, 
  Typography, 
  Card, 
  CardMedia,
  CardHeader, 
  CardContent, 
  CardActions,
  Divider
} from '@material-ui/core'
import Favorite from '@material-ui/icons/Favorite'
import AddIcon from '@material-ui/icons/Add'

const CourseCard = ({classes,courseInfo,openDialog}) => (
  <div className={classes.root} onClick={openDialog}>
    <Card className={classes.card}>
      <CardHeader
        subheader={courseInfo.title}
      />
      <CardMedia
        image={courseInfo.cover}
        className={classes.cardMedia}
      />
      <CardContent>
        <Typography variant="caption">
          {courseInfo.description}
        </Typography>
      </CardContent>
      <Divider />
      <CardActions className={classes.cardActions}>
        <Button>BY {courseInfo.author}</Button>
        <div>
          <IconButton className={classes.cardBtns}>
            <Favorite />
          </IconButton>
          <IconButton className={classes.cardBtns}>
            <AddIcon />
          </IconButton>
        </div>
      </CardActions>
  </Card>
  </div>
)

const styles = theme => ({
  root: {
    display: 'inline-block',
    width:400,
    height: 450,
    padding: theme.spacing.unit * 2,
    cursor: 'pointer'
  },
  card: {
    height: '100%',
  },
  cardMedia: {
    height: 0,
    paddingTop: '56.25%'
  },
  cardActions: {
   justifyContent: 'space-between'
  }
})

export default withStyles(styles)(CourseCard)